<template>
    <div class="login">
        <el-card shadow="hover">
            <div style="text-align:center;font-weight:700">系统登录</div>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" status-icon>
                <el-form-item label="用户名" prop="username" autocomplete="off">
                    <el-input v-model="ruleForm.username" placeholder="请输入账号"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input type="password" v-model="ruleForm.password" autocomplete="off" placeholder="请输入密码"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
import Mock from 'mockjs'
import Cookie from 'js-cookie'
import { getMenu } from '../api/index'
export default {
    data() {
        var validateusername = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('用户名字不能为空'));
            }
            callback();
        };
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            }
            callback();
        };
        return {
            ruleForm: {
                username: '',
                password: '',
            },
            rules: {
                password: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                username: [
                    { validator: validateusername, trigger: 'blur' }
                ],
            }
        };
    },
    methods: {
        //登录
        submitForm(ruleForm) {
            //验证校验规则
            this.$refs[ruleForm].validate((valid) => {
                if (valid) {
                    getMenu(this.ruleForm).then(({ data }) => {
                        if (data.code === 20000) {
                            this.$message({
                                message: '登录成功',
                                type: 'success'
                            });
                            const token = data.data.token
                            //将token 信息存入cookie用于不同信息的通讯
                            Cookie.set('token', token)
                            //获取菜单的数据 存入store中 
                            this.$store.commit('setMenu', data.data.menu)
                            this.$store.commit('addMenu', this.$router)
                            this.$router.push('/home')
                        }
                        else {
                            this.$message({
                                message: '登录失败,用户名或密码错误',
                                type: 'warning'
                            });
                        }
                    })
                    // //token 信息 (这里通过mock模拟随机数)
                    // const token = Mock.Random.guid()

                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        }
    }
}
</script>

<style lang="less" scoped>
.login {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -60%);
    width: 400px;
    height: 300px;

    .el-card {
        border-radius: 10px;

        .el-form {
            margin-top: 40px;
            margin-right: 60px;

            .el-button {
                margin-left: 50px;
            }
        }
    }


}
</style>